<template>
  <div>
    <v-row>
      <v-col cols="12" sm="12">
        <v-card outlined >
          <v-card-title> </v-card-title>
          <v-card-text>
            <!-- <p class="text-h5">Cross</p> -->
            <div >
              <!-- <h3 style="border-left: 5px solid #1890ff" class="pl-3">
                BRAC CROSS RESULTS

              </h3> -->
              <v-row class="ml-3 mt-2">
                <v-col cols="12" sm="2">
                  <v-autocomplete
                    hide-details
                    v-model="SelectedYear"
                    dense
                    :items="Years"
                    label="Year"
                  ></v-autocomplete>
                </v-col>
                <v-col cols="12" sm="4">
                  <v-autocomplete
                    hide-details
                    v-model="EventSelected"
                    :loading="EventLoader"
                    :disabled="EventLoader"
                    dense
                    :items="Events"
                    item-value="id"
                    item-text="name"
                    label="Event"
                  >
                  <template v-slot:item="data">
                          <v-list-item-content>
                            <v-list-item-title>
                              
                              {{ data.item.name }}</v-list-item-title
                            >
                            <v-list-item-subtitle>
                              {{  formatDate(data.item.eventdatetime)  }}-@ {{ data.item.eventCity }}
                            </v-list-item-subtitle>
                          </v-list-item-content>
                        </template>
                  </v-autocomplete>
                </v-col>
                <v-col cols="12" sm="2">
                  <v-autocomplete
                    hide-details
                    v-model="GroupSelcted"
                    :loading="GroupLoader"
                    :disabled="GroupLoader"
                    dense
                    :items="Groups"
                    item-value="id"
                    item-text="name"
                    label="Group"
                  ></v-autocomplete>
                </v-col>
                 <v-col cols="12" sm="1">
                  <v-btn small  target="__blank__" :href="`/AcaResultNoPage/?resultsetid=${GroupSelcted}&export=csv&type=road`" v-if="GroupSelcted">Export CSV</v-btn>
                </v-col>
              </v-row>
              <v-row class="mt-2">
                <v-col>
                  <!-- <v-text-field                    
                  v-model="ResultSearch"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                ></v-text-field> -->
                  <!-- <b>RACE SUMMARY:</b> -->
                  <v-btn-toggle v-show="false" v-model="summaryBy"  dense class="ml-2" borderless>
                    <v-btn >
                      <span class="hidden-sm-and-down">Summary By RaceGroup</span>

                    </v-btn>

                    <!-- <v-btn >
                      <span class="hidden-sm-and-down">Summary By City</span>

                    </v-btn>

                    <v-btn >
                      <span class="hidden-sm-and-down">Summary By Team</span>

                    </v-btn> -->

                  </v-btn-toggle>
                  <v-data-table
                    :loading="EventResultLoader"
                    dense
                    :footer-props="{
                      'items-per-page-options': [15],
                    }"
                    :headers="headers"
                    :items="EventResult.results"
                    item-key="name"
                    :search="ResultSearch"
                    :options.sync="pagination_results"
                    :server-items-length="EventResult.count"
                    class="elevation-1 mt-1"
                  >
                    <template v-slot:item.view_result="{ item }">
                      <a @click="selectedUserModal(item)"
                        >${item.view_result}</a
                      >
                    </template>
                  </v-data-table>
                </v-col>
              </v-row>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import CycAxios from '@/plugins/axios.js'
import eventData from '@/data/aca_event.json'; // Assuming you've stored the JSON data in a file named 'events.json'
import get_years from '@/data/get_years.json'
import aca_result from '@/data/aca_result.json'; // Assuming you've stored the JSON data in a file named 'events.json'
import aca_user from '@/data/aca_user.json'; // Assuming you've stored the JSON data in a file named 'user.json'
import aca_resultset from '@/data/aca_resultset.json'; // Assuming you've stored the JSON data in a file named 'events.json'

export default {
  mounted() {
    this.getYear();
  },
    data() {
    return {
      Years: [],
      SelectedYear: null,
      Events: [],
      EventLoader: false,
      EventSelected: null,
      EventResult: [],
      EventResultLoader: false,
      GroupSelcted: null,
      Groups: [],
      GroupLoader: false,
      summaryBy: 0,
      pagination_results: {},
      headers: [
        { text: "CATEGORY", value: "eventracegroupid.abbreviation" },
        { text: "STARTERS	", value: "STARTERS" },
        { text: "FINISHERS", value: "FINISHERS" },
      ],
      RaceByCate: [
        { text: "CATEGORY", value: "eventracegroupid.abbreviation" },
        { text: "STARTERS	", value: "STARTERS	" },
        { text: "FINISHERS", value: "FINISHERS" },
      ],
      RaceByCateWithGroup: [
        { text: "Place", value: "place" },
        { text: "TIME", value: "time" },
        { text: "CATEGORY", value: "eventracegroupid.mincategory" },
        { text: "FIRST NAME", value: "firstName" },
        { text: "Last NAME", value: "lastName" },
        // { text: "TEAM", value: "" },
        { text: "CITY", value: "city" },
        // { text: "DETAILS", value: "" },
        
      ],
      RaceByCity: [
        { text: "Home City", value: "CATEGORY" },
        { text: "FINISHERS	", value: "STARTERS	" },
      ],
      RaceByTeam: [
        { text: "TEAM", value: "CATEGORY" },
        { text: "FINISHERS", value: "STARTERS" },
      ],
      ResultSearch: "",
    };
  },

  watch: {
    summaryBy(value) {
      value;
      this.getHeaders();
    },
    // EventSelected(value) {
      
    //   this.GroupLoader = true;
    //   CycAxios.get(`/AcaEvent/${value}/get_groups/?fields=id,name,starters`).then(
    //     (data) => {
    //         this.GroupSelcted = 0
    //       this.GroupLoader = false;
    //       this.Groups = data.data;
    //       this.Groups.push({ name: "Event Summary", id: -1 });
    //       this.GroupSelcted = -1;
    //       this.EventResult = [];
    //       this.EventResult = { results: this.Groups };
    //     }
    //   );
    // },
    EventSelected(value) {
      this.GroupLoader = true;
      // Filter result sets based on the selected event ID
      this.Groups = aca_resultset.aca_resultset.filter(resultSet => resultSet.eventId === value);
      // Add "Event Summary" option
      this.Groups.push({ name: "Event Summary", id: -1 });
      this.GroupLoader = false;
      this.GroupSelcted = -1;
      this.EventResult = { results: this.Groups };
    },
    // GroupSelcted(value) {
      
    //   this.EventResult = [];
    //   if (value == -1) {
    //     console.log(value);
    //     this.EventResult = { results: this.Groups };
    //     this.headers = [
    //       { text: "CATEGORY", value: "name" },
    //       { text: "STARTERS	", value: "starters" },
    //       { text: "FINISHERS", value: "starters" },
    //     ];
    //   }
    //   else{
    //     this.EventResultLoader = true;
    //     CycAxios.get("/AcaResultNoPage/?resultsetid="+value).then(
    //         (data) => {
    //           this.EventResultLoader = false;
    //           this.EventResult = {results: data.data};
    //           this.headers= this.RaceByCateWithGroup
    //         }
    //       );  
    //   }
    // },
    GroupSelcted(value) {
      this.EventResult = [];
      if (value == -1) {
        console.log(value);
        this.EventResult = { results: this.Groups };
        this.headers = [
          { text: "CATEGORY", value: "name" },
          { text: "STARTERS", value: "starters" },
          { text: "FINISHERS", value: "starters" },
        ];
      } else {
        const filteredResults = aca_result.aca_result.filter(result => result.resultSetId === value);
        this.EventResult = { results: this.mapUserDetails(filteredResults) };
        this.headers = this.RaceByCateWithGroup
      }
    },

    // SelectedYear(value) {
    //   this.EventLoader = true;
    //   CycAxios.get("/AcaEventNoPage/?eventtype=52&ordering=-eventdatetime&eventdatetime__year=" + value).then(
    //     (data) => {
    //       this.EventLoader = false;
    //       this.Events = data.data;
    //     }
    //   );
    // },
    SelectedYear(value) {
      // Filter events based on the selected year
      this.EventLoader = true;
      this.Events = eventData.aca_event.filter(event => {
        const eventYear = new Date(event.eventdatetime).getFullYear();
        return eventYear.toString() === value.toString() && event.eventType === "66";
      });
      this.EventLoader = false;
    },
  },
  methods: {
    mapUserDetails(results) {
      return results.map(result => {
        const user = aca_user.aca_user.find(u => u.id === result.racerId);
        return {
          ...result,
          firstName: user ? user.firstName : "",
          lastName: user ? user.lastName : "",
          city: user ? user.city : "",
        };
      });
    },
    formatDate(datetime) {
      const dateObj = new Date(datetime);
      const options = { day: "numeric", month: "long", year: "numeric" };
      return dateObj.toLocaleDateString("en-US", options);
    },
    getHeaders() {
      if (this.summaryBy == 0) {
        this.headers = this.RaceByCate;
      } else if (this.summaryBy == 1) {
        this.headers = this.RaceByCity;
      } else if (this.summaryBy == 2) {
        this.headers = this.RaceByTeam;
      }
    },
    getYear() {
      this.Years = get_years;
    }
  },
};
</script>

